export type ServerErrors = {
  errors: {
    extensions: {
      code: ErrorCode;
    };

    name: string;
    fieldName?: string;
    stack: string;
    message: string;
  }[];
};

enum ErrorCode {
  ERR_INCORRECT_EMAIL_OR_PASSWORD = 'ERR_INCORRECT_EMAIL_OR_PASSWORD', //    email  
  ERR_ACCOUNT_ALREADY_EXIST = 'ERR_ACCOUNT_ALREADY_EXIST', //      
  ERR_FIELD_REQUIRED = 'ERR_FIELD_REQUIRED', //  .      fieldName  ,    
  ERR_INCORRECT_PASSWORD = 'ERR_INCORRECT_PASSWORD', //       
  ERR_INVALID_PASSWORD = 'ERR_INVALID_PASSWORD', //      /^[\w-@{}()#$%^&*+=!~]{8,}$/
  ERR_NOT_VALID = 'ERR_NOT_VALID', //   id 
  ERR_AUTH = 'ERR_AUTH', //   ,    
  ERR_NO_FILES = 'ERR_NO_FILES', //    
  ERR_NOT_ALLOWED = 'ERR_NOT_ALLOWED', //      (    )
  ERR_NOT_FOUND = 'ERR_NOT_FOUND', //   
  ERR_VALIDATION_ERROR = 'ERR_VALIDATION_ERROR', //   , ,   name
  ERR_INVALID_QUERY_PARAMS = 'ERR_INVALID_QUERY_PARAMS', //  GET       search params   { [key: string]: string //   JSON.stringify() }
  ERR_INTERNAL_SERVER = 'ERR_INTERNAL_SERVER', //  .   ,     
}

